document.addEventListener('DOMContentLoaded', ()=>{
    iniciarApp();
})

function iniciarApp(){
    barraFija();
    navScroll();
    seleccionSeccion();
}

function barraFija(){
    const header = document.querySelector('.header');
    const barra = document.querySelector('.header_barra');
    const body = document.querySelector('body')

    window.addEventListener('scroll', function(){
        const posicion = header.getBoundingClientRect().bottom;

        if(posicion < 200){
            barra.classList.add('fijo')
            body.classList.add('barra_fija')
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('barra_fija')
        }
    })
}

function navScroll(){
    const enlaces = document.querySelectorAll('.navegacion');

    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e){
            e.preventDefault();

            const ubicacion = e.target.attributes.href.value;
            const seccion = document.querySelector(ubicacion)
            seccion.scrollIntoView({behavior: "smooth"})

        })
    })
}

function seleccionSeccion() {
    const secciones = document.querySelectorAll('.navegacion a');

    secciones.forEach( seccion => {
        seccion.addEventListener('click', function(e){
            
            const ubicacion = e.target.attributes.href.value;
            const seccion = document.querySelector(ubicacion);

                seccion.classList.add('seleccionado');
            
        })
    })
}